import React, { useState, useRef } from 'react';
import {
  Editor,
  EditorState,
  Modifier,
  ContentState,
  CompositeDecorator,
  convertFromHTML,
  ContentBlock,
  genKey,
  AtomicBlockUtils,
  SelectionState,
  RichUtils
} from 'draft-js';
import { List } from 'immutable';

// URL detection strategy
const findLinkEntities = (contentBlock, callback, contentState) => {
  const text = contentBlock.getText();
  const urlRegex = /https?:\/\/[^\s]+/g;
  let matchArr, start;
  while ((matchArr = urlRegex.exec(text)) !== null) {
    start = matchArr.index;
    callback(start, start + matchArr[0].length);
  }
};

// Link component
const Link = (props) => {
  return (
    <a href={props.decoratedText} style={{ color: '#3b5998', textDecoration: 'underline' }} target="_blank" rel="noopener noreferrer">
      {props.children}
    </a>
  );
};







// Decorator
const decorator = new CompositeDecorator([
  {
    strategy: findLinkEntities,
    component: Link,
  },
]);







/// buttons:

const insertHeader = (editorState, headerType) => {
    return RichUtils.toggleBlockType(
      editorState,
      headerType
    );
  };

const insertNewParagraph = (editorState) => {
    const contentState = editorState.getCurrentContent();
    const newBlock = new ContentBlock({
      key: genKey(),
      type: 'unstyled',
      text: '',
      characterList: List(),
    });
  
    const blockArray = contentState.getBlocksAsArray();
    blockArray.push(newBlock);
  
    const newContentState = ContentState.createFromBlockArray(blockArray);
    return EditorState.push(editorState, newContentState, 'insert-fragment');
  };
  
  // Utility to insert an image (as an example of media)
  
  
  
  // Utility to delete the current paragraph/block
  const deleteCurrentBlock = (editorState) => {
    const selectionState = editorState.getSelection();
    const contentState = editorState.getCurrentContent();
    const blockKey = selectionState.getStartKey();
    const newContentState = Modifier.removeRange(
      contentState,
      new SelectionState({
        anchorKey: blockKey,
        anchorOffset: 0,
        focusKey: blockKey,
        focusOffset: contentState.getBlockForKey(blockKey).getLength(),
      }),
      'backward',
    );
  
    return EditorState.push(editorState, newContentState, 'remove-range');
  };








const RichTextEditorWithLinks = () => {
  const [editorState, setEditorState] = useState(EditorState.createEmpty(decorator));
  const fileInputRef = useRef();
  const handlePastedText = (text, html, editorState) => {
    const contentState = editorState.getCurrentContent();
    const blocksFromHTML = convertFromHTML(html || text);
    const pastedContentState = ContentState.createFromBlockArray(
      blocksFromHTML.contentBlocks,
      blocksFromHTML.entityMap
    );

    const newState = Modifier.replaceWithFragment(
      contentState,
      editorState.getSelection(),
      pastedContentState.getBlockMap()
    );

    setEditorState(EditorState.push(editorState, newState, 'insert-fragment'));
    return 'handled';
  };

  const insertImage = (editorState, imageUrl) => {
    const contentState = editorState.getCurrentContent();
    const contentStateWithEntity = contentState.createEntity('IMAGE', 'IMMUTABLE', { src: imageUrl });
    const entityKey = contentStateWithEntity.getLastCreatedEntityKey();
    const newEditorState = EditorState.set(editorState, { currentContent: contentStateWithEntity });
  
    const newContentState = Modifier.insertText(
        newEditorState.getCurrentContent(),
        newEditorState.getSelection(),
        ' ',
        null,
        entityKey
      );
    
    return EditorState.push(newEditorState, newContentState, 'insert-characters');
  };

  const triggerFileInput = () => {
    fileInputRef.current.click();
  };
  
  
  const handleImageUpload = (event) => {
      const file = event.target.files[0];
      if (file) {
        const reader = new FileReader();
        reader.onload = (e) => {
          const imageUrl = e.target.result;
          setEditorState(insertImage(editorState, imageUrl));
        };
        reader.readAsDataURL(file);
      }
    };

  const handleOnChange = (state) => {
    setEditorState(state);
  };

  return (
    <div>
        <div style={{ border: '1px solid black', padding: '10px' }}>
      <Editor
        editorState={editorState}
        onChange={handleOnChange}
        handlePastedText={(text, html) => handlePastedText(text, html, editorState)}
      />
        
        
        </div>
        <button onClick={() => setEditorState(insertNewParagraph(editorState))}>
            New Paragraph
        </button>
        <button onClick={() => setEditorState(insertHeader(editorState, 'header-one'))}>
            Insert H1 Header
        </button>
        <button onClick={() => setEditorState(insertHeader(editorState, 'header-two'))}>
            Insert H2 Header
        </button>
        <button onClick={triggerFileInput}>
            Insert Image
        </button>
        <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            style={{ display: 'none' }}
            onChange={handleImageUpload}
        />
        <button onClick={() => setEditorState(deleteCurrentBlock(editorState))}>
            Delete Current Paragraph
        </button>
    </div>
    
  );
};

export default RichTextEditorWithLinks;
